.main-header-wrapper {
	position: sticky;
	top: 0;
	display: flex;
	justify-content: center;
	align-items: center;

	/* background-color: rgb(20, 20, 20); */
	background-color:rgba(0,0,0,0.7);

	height: 10vh;
	color: var(--primary-color);
	transition: all 0.5s;
	transition-timing-function: ease-in;
	z-index: 1;
}
.nav__black {
	background-color: rgb(20, 20, 20);
}
.header-continer {
	width: 90%;
	display: flex;
	justify-content: space-between;
	align-items: center;
}
.left-header-wrapper ul {
	width: 140%;
	display: flex;
	justify-content: space-between;
	align-items: center;
	list-style: none;
	cursor: pointer;
}
.right-header-wrapper ul {
	width: 150%;
	display: flex;
	justify-content: space-around;
	list-style: none;
	cursor: pointer;
}
